// styling
import styles from './style.module.scss';

// components
import SectionHeader from '@components/SectionHeader';
import LinkWithArrow from '@ui/LinkWithArrow';
import CollectionItem from '@components/CollectionItem';
import { useEffect, useState } from 'react';

// data placeholder
import featured from '@db/featured';
import axios from 'axios';


const FeaturedCollections = () => {
    const [collectionData, setCollectionData] = useState([]);
    useEffect(() => {
       
        (async () => {
          
            const { data } = await axios.get(
              "https://api-goerli.reservoir.tools/collections/v5",
              {
                params: {
                  sortBy: "allTimeVolume",
                  limit: 6
                },
              }
            );

            setCollectionData(data.collections)
        })();
      }, []);
    
    return (
        <section>
            <div className="container">
                <SectionHeader title="Top Collections ⚡">
                    <LinkWithArrow href="/ranking" text="View all collections"/>
                </SectionHeader>
                <div className={styles.grid}>
                    {
                        collectionData.map((item, index) => (
                            <CollectionItem item={item} index={index} key={index}/>
                        ))
                    }
                </div>
            </div>
        </section>
    )
}

export default FeaturedCollections